import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import PageTopBanner from "./PageTopBanner";
import SearchForm from "./SearchForm";
import SecIntro from "./SecIntro";
import DescriptionBlock from "./DescriptionBlock";
import ProfileCards from "./ProfileCards";

const contentData = {
  bottomDesc: [
    "Find, compare and book driving lessons with the best driving instructors near you.",
  ],
};
const introData = {
  titleStart: "Still learning and not ready for a test package yet?",
  highlight: "",
  titleEnd: "",
  theme: "",
};

function SearchResult() {
  return (
    <>
      <PageTopBanner>
        <Container>
          <Row>
            <Col lg={12}>
              <div className="PageBanner__SearchResult">
                <SearchForm />
              </div>
            </Col>
          </Row>
        </Container>
      </PageTopBanner>
      <div className="bg-grey titleCenter profileCard">
        <Container>
          <Row>
            <Col lg={12}>
              <SecIntro
                titleStart={introData.titleStart}
                highlight={introData.highlight}
              />
              <DescriptionBlock content={contentData.bottomDesc} />
              <ProfileCards />
            </Col>
          </Row>
        </Container>
      </div>
    </>
  );
}

export default SearchResult;
